import React, { Component } from "react";
import { HashRouter, Route, Switch, Redirect, Router, BrowserRouter} from "react-router-dom";
import { history } from "../history";
import LandingPage from "../pages/LandingPage";
import NavBar from "../components/NavBar";
import Footer from "../components/Footer";
import MobileUnderContruction from "../pages/MobileUnderContruction";
import UnderConstructionPage from '../pages/UnderConstructionPage';
import {LOCAL} from '../configs';

class Portal extends Component {
  constructor() {
    super();
    this.state = {
      isLoading: false,
      isAuthenticated: false,
      data: false
    };
  }

  componentDidMount() {
    this.retrieveData()
    .then(res => { console.log(res[0].express)})
    console.log("Component has mounted: Portal");
  }

  retrieveData = async () => {
    const fetchUrl = await fetch('/express_backend'); 
    const response = await fetchUrl.json(); 

    if (!response.state === 200) {
      new Error(response.body); 
    }

    return response; 
  }

  // callBackendAPI = async () => {
  //   const response = await fetch("/express_backend");
  //   console.log(response.json())
  //   // const body = await response.json();

  //   // if (response.status !== 200) {
  //   //   throw Error(body.message);
  //   // }

  //   return;
  // };

  render() {
    return (
      <React.Fragment>
        {console.log(this.state.data)}
        <NavBar />
        <HashRouter history={history}>
          <Switch>
            <Route exact path="/landing" component={LandingPage} />
            <Route
              exact
              path="/mobileUnderContruction"
              component={MobileUnderContruction}
            />
            <Route
              exact
              path="/underConstruction"
              component={UnderConstructionPage}
            />

            {/* {!this.state.isAuthenticated ? (
              <Redirect to="/" />
            ) : (
              <Redirect to="/adminPage" />
            )} */}
            {LOCAL == true ? (
              <Redirect from="/" to="/landing"></Redirect>
            ) : (
              <Redirect
                from="CyberronOfficial/#/"
                to="CyberronOfficial/#/landing"
              ></Redirect>
            )}
          </Switch>
        </HashRouter>
        <Footer />
      </React.Fragment>
    );
  }
}

export default Portal;
