import React, {Component} from 'react'; 
import {history} from '../../history';
import PageLoader from '../PageLoader'; 
import './style.css'; 

class Card extends Component {
    constructor(props) {
        super(props); 
        this.state = {
            isLoading: false, 
            url: '',

        }; 
    }

    routeToServices(url) {
      this.setState({
        url: url, 
        isLoading: true, 
      })
    }

    routeToMembership(url) {
     this.setState({
       url: url, 
       isLoading: true,
     })
    }

    routeToFounders(url) {
      this.setState({
        url: url,
        isLoading: true,
      })
    }

  checkIsLoading(isLoading) {
    if (isLoading == false) {
      this.setState({
        isLoading: false,
      })
    }
  }

    render() {
        // if (this.state.isLoading) {
        //   return (<PageLoader url={this.state.url} isLoading={(isLoading) => this.checkIsLoading(isLoading) }></PageLoader>)
        // }
        return (
          <React.Fragment>
            <div
              className="Card city"
              onClick={() => this.routeToServices(`CyberronOfficial/#/underConstruction`)}
          >
              <div className="title">{`View Services`}</div>
            </div>
            <div
              className="Card phone"
              onClick={() => this.routeToMembership(`CyberronOfficial/#/underConstruction`)}
            >
              <div className="title">{"Become a Member"}</div>
            </div>
            <div
              className="Card office"
              onClick={() => this.routeToFounders(`CyberronOfficial/#/underConstruction`)}
            >
              <div className="title">{"Our Founders"}</div>
            </div>
          </React.Fragment>
        ); 
    }
}

export default Card; 




